.movie-detail {
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.movie-detail h1 {
    font-size: 2rem;
    margin-bottom: 10px;
}

.movie-detail h2 {
    font-size: 1.5rem;
    color: #777;
    margin-bottom: 20px;
}

.movie-detail img {
    max-width: 100%;
    border-radius: 5px;
    margin-bottom: 20px;
}

.movie-detail ul {
    list-style: none;
    padding: 0;
}

.movie-detail ul li {
    display: inline;
    margin-right: 10px;
    background-color: #ff7675;
    color: white;
    padding: 5px 10px;
    border-radius: 12px;
}
